	module.exports = {"data":{"post":{"html":"<p>Unlike Mama, who spent her vacation reading mysteries, I got myself the two new PHP books from O'Reilly, <a href=\"http://www.oreilly.com/catalog/progphp/\">Programming PHP</a> and <a href=\"http://www.oreilly.com/catalog/webdbapps/\">Web Database Applications</a>. They are both worthwhile, although the Database book is more in tune with what I am doing, while Programming PHP is more of an introduction. For various reasons, PHP 4 is becoming my main tool at work (although perl still plays a big role). </p>\n<p>I'm sure Mama had more fun with her reading, though.</p>","fields":{"title":"PHP Reading","date":"10 May 2002","url":"https://papascott.de/2002-05-10-php-reading/"}}},"pathContext":{"slug":"/2002-05-10-php-reading/","prev":{"fields":{"title":"Feeling Less Safe","date":"10 May 2002","slug":"/2002-05-10-feeling-less-safe/"}},"next":{"fields":{"title":"This and That from Phoenix","date":"11 May 2002","slug":"/2002-05-11-this-and-that-from-phoenix/"}}}}

